* {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  line-height: 1.5;
  font-family: $arial;
  font-weight: 400;
  color: #000;
  font-style: normal;
  background-image: url(../img/noise-bg.jpg);
  background-repeat: repeat;
}

ul, p {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

@media #{tablet-widths} {
  body {
    min-width: 620px;
  }
}

@media #{desktop-widths} {
  body {
    min-width: 940px;
  }
}



