.franchise {
  width: 722px;
  height: 410px;
  margin: 0 auto;
  margin-top: -190px;
  text-align: center;
  background-image: url(../img/laptop.png);
  background-repeat: no-repeat;

  &__wrap {
    border-bottom: 1px solid #5c37ef;
  }
  &__link {
    margin-top: 155px;
    font-family: Tahoma;
    font-size: 16px;
    text-decoration: none;
    color: #5c37ef;

    @include bottom-border;
  }

}