@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin container {
  width: 280px;
  margin: auto;

  @media #{tablet-widths} {
    width: 620px;
  }

  @media #{desktop-widths} {
    width: 940px;
  }
}

@mixin bottom-border {
  & {
    position: relative;
    display: inline-block;
    text-decoration: none;
  }
  &::before {
    content: "";
    position: absolute;
    display: inline-block;
    left: 0;
    right: 0;
    bottom: 1px;
    border-bottom: 1px solid #396ea9;
  }
}