
.main {

  &__container {
    width: 1000px;
    margin: 0 auto;
    margin-top: 20px;
    padding: 0 103px 22px;
    font-size: 16px;
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.12);
    background-color: #f7f8fa;

    &::before {
      content: "";
      display: table;
    }
  }

  &__top-block {
    width: 1000px;
    min-height: 420px;
    margin: -20px -103px 0;
    padding: 20px 0; 
    border-radius: 5px 5px 0 0;
    color: #fff;
    background-image: url(../img/main-page-bg-2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
  }

  &__top-block h2 {
    text-align: center;
    margin: 0;
  }

  &__top-block h2 p:first-child {
    margin-top: 32px;
    font-size: 30px;
    font-weight: normal;
  }
  &__top-block h2 p:last-child {
    margin-top: 22px;
    font-size: 50px;
  }

  &__footer {
    padding-top: 30px;
    padding-bottom: 30px;
  }


}