.columns {
  padding-top: 36px;
  padding-bottom: 6px;

  @include clearfix;

  &__col {
    float: left;
    width: 385px;
  }

  &__col:first-child {
    margin-right: 20px;
  }

  &__title {
    margin-top: 24px;
    margin-bottom: 0;
    font-size: 24px;
    font-weight: normal;
  }

}

